// You can change the following settings by yourself :-)

:root {
  --base-font-family: -apple-system, BlinkMacSystemFont, 'Helvetica Neue', Arial,
    'PingFang SC', 'Hiragino Sans GB', STHeiti, 'Microsoft YaHei',
    'Microsoft JhengHei', 'Source Han Sans SC', 'Noto Sans CJK SC',
    'Source Han Sans CN', 'Noto Sans SC', 'Source Han Sans TC',
    'Noto Sans CJK TC', 'WenQuanYi Micro Hei', SimSun, sans-serif;
}

// ========== DIY ========== //
$feature-color: #f75357;
$feature-color-dark: #ff7875; // Softer Morandi red for better dark mode readability
$code-theme: 'atom-one-dark'; // atom-one-dark or atom-one-light

// ========== global variables ========== //
$body-background-color: #fff;
$foreground-color: #fdfdfd;
$body-background-color-dark: #121212;
$foreground-color-dark: #181818;
$base-font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Helvetica Neue', Arial,
  'PingFang SC', 'Hiragino Sans GB', STHeiti, 'Microsoft YaHei',
  'Microsoft JhengHei', 'Source Han Sans SC', 'Noto Sans CJK SC',
  'Source Han Sans CN', 'Noto Sans SC', 'Source Han Sans TC', 'Noto Sans CJK TC',
  'WenQuanYi Micro Hei', SimSun, sans-serif;
$feature-font-family: 'Oswald-Regular', $base-font-family;
$base-font-size: 16px !default;

// ========== CSS Variables ========== //
$base-font-family: var(--base-font-family);

// ========== common ========== //
$container-margin-top: 3rem;
$selection-background-color: $feature-color;
$selection-background-color-dark: $feature-color-dark;
$selection-text-color: #eee;
$selection-text-color-dark: snow;

// ========= dark mode ========= //
$text-color-primary-dark: rgba(255, 255, 255, 0.87);
$text-color-secondary-dark: rgba(255, 255, 255, 0.6);
$text-color-minor-dark: rgba(255, 255, 255, 0.38);
$transition-animation: all 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);

// ========== iconfont ========== //
@font-face {
  font-family: 'iconfont-archer';
  /* project id 327081 */
  src: url('//at.alicdn.com/t/font_327081_s1wbjxwfu9c.eot');
  src:
    url('//at.alicdn.com/t/font_327081_s1wbjxwfu9c.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_327081_s1wbjxwfu9c.woff') format('woff'),
    url('//at.alicdn.com/t/font_327081_s1wbjxwfu9c.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_327081_s1wbjxwfu9c.svg#iconfont-archer') format('svg');
  font-display: swap;
}

// ========== text fonts ========== //
// Declare Oswald-Regular used throughout the theme with swap to avoid FOIT.
@font-face {
  font-family: 'Oswald-Regular';
  src:
    url('/font/Oswald-Regular.woff2') format('woff2'),
    url('/font/Oswald-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

// Monospace code font (local assets present in source/font)
@font-face {
  font-family: 'Source Code Pro';
  src:
    url('/font/SourceCodePro-Regular.ttf.woff2') format('woff2'),
    url('/font/SourceCodePro-Regular.ttf.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

// Optional sans font shipped with theme assets
@font-face {
  font-family: 'Source Sans Pro';
  src:
    url('/font/Source Sans Pro.woff2') format('woff2'),
    url('/font/Source Sans Pro.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

.iconfont-archer {
  font-family: 'iconfont-archer' !important;
  font-size: 1rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// ========== header ========== //
$header-height: 3.5rem;
$header-height-mobile: 3rem;
$header-border-top-width: 3px;
$header-border-top-color: rgba(0, 0, 0, 0.75);
$header-border-top-color-dark: rgba(0, 0, 0, 0.75);
$header-banner-text-color: #333;
$header-banner-text-color-dark: $text-color-primary-dark;

// ========= sidebar ========= //
$sidebar-width: 400px;

// ========== profile ========== //
$profile-width: 200px;

// ========== index ========== //
$index-post-link-color: #1f1f1f;
$index-post-link-color-dark: #ccc;
$index-post-tag-color: #555; // Darkened for better contrast
$index-post-tag-color-dark: $text-color-minor-dark;

// ===== abstract content ===== //
$abstract-content-title-color: #333;
$abstract-content-title-color-dark: $text-color-primary-dark;
$abstract-content-text-color: #333; // Darkened for better readability
$abstract-content-text-color-dark: $text-color-secondary-dark;
$abstract-post-meta-color: #666; // Improved contrast ratio: 5.74:1 (was #aaa at 2.32:1)
$abstract-post-meta-color-dark: $text-color-minor-dark;

// ========== post ========== //
$post-content-color: #23394d;
$post-content-color-dark: $text-color-primary-dark;

// ========== social ========== //
$social-iconfont-color: #000000;
$social-iconfont-color-dark: #dadada;

// ========== code ========== //
$code-color-map: (
  'atom-one-light': (base: #fafafa,
    mono-1: #383a42,
    mono-2: #686b77,
    mono-3: #a0a1a7,
    hue-1: #0184bb,
    hue-2: #4078f2,
    hue-3: #a626a4,
    hue-4: #50a14f,
    hue-5: #e45649,
    hue-5-2: #c91243,
    hue-6: #986801,
    hue-6-2: #c18401,
  ),
  'atom-one-dark': (base: #282c34,
    mono-1: #abb2bf,
    mono-2: #818896,
    mono-3: #5c6370,
    hue-1: #56b6c2,
    hue-2: #61aeee,
    hue-3: #c678dd,
    hue-4: #98c379,
    hue-5: #e06c75,
    hue-5-2: #be5046,
    hue-6: #d19a66,
    hue-6-2: #e6c07b,
  ),
);

@if not(map-has-key($code-color-map, $code-theme)) {
  $code-theme: 'atom-one-dark';
}

$theme-colors: map-get($code-color-map, $code-theme);

// ========== algolia-search ========== //
$algolia-search-padding-horiz: 20px;